import { CFG } from './config.mjs';
import { DataBrowser } from './data-browser.mjs';
import { DataEditor } from './data-editor.mjs';

/**
 * @param {Actor|Item|object} options
 */
function openInspector(options = {}) {
	if (options instanceof foundry.abstract.DataModel) options = { document: options };
	options.object ??= options.document;

	let app = [...foundry.applications.instances].find(app => app.object === options.object);
	app ??= new DataBrowser(options);

	app.render({ render: true, force: true });
};

function injectHeaderDataButton(sheet, buttons) {
	const allowUsers = game.settings.get(CFG.id, CFG.SETTINGS.allowUsers);
	if (!allowUsers && !game.user.isGM) return;

	// Exhaustive document lookup
	const document = sheet.document ?? sheet.actor ?? sheet.item ?? sheet.object;
	if (!(document instanceof foundry.abstract.Document)) throw new Error('Could not locate sheet\'s document');

	buttons.unshift({
		class: 'data-inspector',
		icon: 'fa-solid fa-atom',
		label: game.i18n.localize('DataInspector.DataButton'),
		onclick: _ => openInspector(document),
	});
};

/**
 * @param {foundry.applications.api.ApplicationV2} app
 * @param {object[]} entries
 */
function injectContextDataButton(app, entries) {
	let collection = app.collection ?? game.packs.get(packId);

	/* global DocumentCollection */
	if (!(collection instanceof DocumentCollection)) collection = collection.collection;
	if (!collection) return;

	const packId = collection.metadata?.id;

	const documentType = collection.documentName;
	if (!['Item', 'Actor'].includes(documentType)) return;

	entries.push({
		name: 'DataInspector.Context.OpenInspector',
		icon: '<i class="fa-solid fa-atom"></i>',
		condition: () => {
			const allowUsers = game.settings.get(CFG.id, CFG.SETTINGS.allowUsers);
			return allowUsers || game.user.isGM;
		},
		callback: async (entry) => {
			if (entry instanceof jQuery) entry = entry[0];// v12/v13 cross-compatibility
			const documentId = entry.dataset.entryId || entry.dataset.documentId;

			let doc;
			if (packId)
				doc = await collection.getDocument(documentId);
			else
				doc = collection.get(documentId);

			if (doc)
				openInspector(doc);
			else
				console.warn('Document not found:', { documentId, packId, id: app.id });
		},
	});
};

Hooks.once('ready', async () => {
	const C = CFG.COLORS;

	const allowUsers = game.settings.get(CFG.id, CFG.SETTINGS.allowUsers);
	if (!allowUsers && !game.user.isGM)
		return void console.log(`%cDATA INSPECTOR%c 🔍 | %c${mod.version}%c | User access blocked in settings.`, C.main, C.unset, C.id, C.unset);

	// Register API
	const mod = game.modules.get(CFG.id);
	mod.api = {
		inspect: openInspector,
		applications: {
			browser: DataBrowser,
			editor: DataEditor,
		},
		handlers: {
			applications: {
				render: injectAppHeaderDataButton,
			},
		},
	};

	// Fetch in-built system specific extensions
	// const systemExtPath = `modules/${CFG.module}/systems/${game.system.id}.mjs`;

	if (['pf1'].includes(game.system.id)) { // Temporary limiter on import() errors
		await import(`./systems/${game.system.id}.mjs`)
			.then(() => console.log('%cDATA INSPECTOR%c 🔍 | System extensions loaded %c✔', C.main, C.unset, 'color:green'))
			.catch(() => console.log('%cDATA INSPECTOR%c 🔍 | System extensions not found %c✘', C.main, C.unset, 'color:red'));
	}

	console.log(`%cDATA INSPECTOR%c 🔍 | %c${mod.version}%c | READY`, C.main, C.unset, C.id, C.unset);

	Hooks.callAll('data-inspector.appHandler', mod.api.handlers.applications.render);
});

/**
 *
 * @param {*} app - Application
 * @param {Function<Application|ApplicationV2>} getObject
 * @param strict
 */
function injectAppHeaderDataButton(app, getObject, strict = false) {
	let html = app.element;
	if (html instanceof jQuery) html = html[0];

	// Exhaustive document lookup
	let object;
	if (getObject) {
		object = getObject(app);
		object.title ??= () => object.name ?? object.title ?? object.label;
	}
	else {
		object = app.document ?? app.object ?? app.actor ?? app.item;
	}

	if (app instanceof foundry.applications.api.ApplicationV2)
		injectAppHeaderDataButtonAppV2(app, html, object, strict);
	else
		injectAppHeaderDataButtonAppV1(app, html, object, strict);
}

function injectAppHeaderDataButtonAppV2(app, html, target, strict = false) {
	const header = html.querySelector('header.window-header');
	if (!header) return;
	if (header.querySelector('button[data-action="__mdiInspectData"]')) return; // Already there
	const rel = header.querySelector('button[data-action="copyUuid"]');
	if (!rel) return; // Didn't find copy UUID button

	const b = document.createElement('button');
	b.type = 'button';
	b.dataset.action = '__mdiInspectData';
	b.dataset.tooltip = game.i18n.localize('DataInspector.Context.OpenInspector');
	b.classList.add('header-control', 'fa-solid', 'fa-atom', 'data-inspector', 'icon');
	rel.before(b);

	app.options.actions.__mdiInspectData ??= function (_event, _el) {
		openInspector(target);
	};
}

function injectAppHeaderDataButtonAppV1(app, html, target, strict = false) {
	const header = html.querySelector('header.window-header');
	if (!header) return;

	if (header.querySelector('a.data-inspector.control.icon')) return; // Already there
	const close = header.querySelector('.header-button.control.close');
	const title = !close ? header.querySelector('.window-title') : null;
	if (!close && !title) return; // Didn't find copy UUID button

	const allowUsers = game.settings.get(CFG.id, CFG.SETTINGS.allowUsers);
	if (!allowUsers && !game.user.isGM) return;

	const b = document.createElement('a');
	b.dataset.tooltip = game.i18n.localize('DataInspector.Context.OpenInspector');
	b.classList.add('header-button', 'control', 'data-inspector', 'icon');
	const i = document.createElement('i');
	i.classList.add('fa-solid', 'fa-atom', 'fa-fw');
	i.inert = true;
	b.append(i, ' ', game.i18n.localize('DataInspector.DataButton'));

	// setTimeout() just to fight Foundry on click handlin priority
	setTimeout(() => {
		b.addEventListener('click', (event) => {
			event.preventDefault();
			event.stopPropagation();
			event.stopImmediatePropagation();

			openInspector(target);
		});
	},
	// Foundry waits 500, we wait just a tiny bit longer. Sadly not a const or anything so if they change this, it'll suck
	510);

	if (close) close.before(b);
	else if (title) title.after(b);
}

/**
 * HACK for Foundry v12
 *
 * @param {ApplicationV2} app
 * @param {HTMLElement} el
 */
function injectDocumentSheetHeaderDataButtonV2(app, el) {
	if (app instanceof DataBrowser || app instanceof DataEditor) return;

	const doc = app.document;
	if (!(doc instanceof Actor || doc instanceof Item)) return;
	if (!doc?.testUserPermission?.(game.user, 'OBSERVER')) return;

	injectAppHeaderDataButton(app, undefined, true);
}

Hooks.on('renderDocumentSheetV2', injectDocumentSheetHeaderDataButtonV2);

// Header buttons
Hooks.on('getActorSheetHeaderButtons', injectHeaderDataButton); // v12 and prior
Hooks.on('getItemSheetHeaderButtons', injectHeaderDataButton); // v12 and prior

// Context menus
Hooks.on('getItemDirectoryEntryContext', injectContextDataButton); // v12 and prior
Hooks.on('getItemContextOptions', injectContextDataButton); // v13
Hooks.on('getActorDirectoryEntryContext', injectContextDataButton); // v12 and prior
Hooks.on('getActorContextOptions', injectContextDataButton); // v13
Hooks.on('getCompendiumEntryContext', injectContextDataButton); // v12 and prior
Hooks.on('getCompendiumEntryContextOptions', injectContextDataButton); // v13
