import { CFG } from './config.mjs';
import { getTypeName, getTypeId, DATA_TYPES, DATA_TYPE_INVERTED } from './data-types.mjs';

const fu = foundry.utils;

/**
 *
 * @param {object} update Update prop
 * @param {object} parent Parent prop data
 * @param {object} options
 * @param {boolean} options.isDelete Is this a delete operation?
 * @param {boolean} options.inArray
 * @param options.isUpdate
 */
const setUpdateData = (update, parent, { inArray = false, isUpdate = false, isDelete = false } = {}) => {
	const opLabel = isDelete ? 'Delete' : 'Update';

	const { value, last: updateProp } = update;

	// Update or delete array index
	const C = CFG.COLORS;
	if (Array.isArray(updateProp)) {
		const index = Number(parent.child);
		console.log(`${opLabel} index %c${index}%c in %c${parent.path}%c`,
			C.number, C.unset, C.id, C.unset);

		if (isDelete)
			updateProp.splice(index, 1);
		else {
			console.log('=', value);
			updateProp.splice(index, 1, value);
		}
	}
	// Update or delete other prop
	else {
		console.log(`${opLabel} value %c${parent.child}%c in %c${parent.path}%c`,
			C.id, C.unset, C.id, C.unset);

		if (isDelete) {
			delete updateProp[parent.child];
			// Normal delete only necessary if we aren't inside an array
			if (!inArray) updateProp['-=' + parent.child] = null;
		}
		else {
			console.log('... Value =', value);
			updateProp[parent.child] = value;
		}
	}
};

const getParentProperty = (path, docData) => {
	const parts = path.split('.');
	const lastProp = parts.pop();
	const parentPath = parts.join('.');
	const parent = fu.getProperty(docData, parentPath);
	return { path: parentPath, value: parent, child: lastProp };
};

// Restructure array parents
const reconstructParentArrays = (docData, path) => {
	const parts = path.split('.');

	const updateData = {};
	let currentUpdateProp = updateData,
		current = docData;

	let inArray = false;

	do {
		const prop = parts.shift();
		const value = current[prop];
		if (Array.isArray(value)) {
			currentUpdateProp[prop] = foundry.utils.deepClone(value);
			inArray = true;
		}
		else
			currentUpdateProp[prop] ??= {};
		currentUpdateProp = currentUpdateProp[prop];
		current = current[prop];
	} while (parts.length);

	return { updateData, last: currentUpdateProp, inArray };
};

export class DataEditor extends foundry.applications.api.HandlebarsApplicationMixin(foundry.applications.api.DocumentSheetV2) {
	_type;

	static get supportedTypes() { return [DATA_TYPES.number, DATA_TYPES.string, DATA_TYPES.boolean, DATA_TYPES.null, DATA_TYPES.undefined]; }

	constructor({ document, path, type, isToken = false, inArray = false, ...options } = {}) {
		super({ document, inArray, path, ...options });

		this._isToken = isToken;
		this._inArray = inArray;

		// this.object = { value: undefined };
		const value = fu.getProperty(document.toObject(), path);
		this._originalTypeId = type ?? getTypeId(value);
		this._originalTypeName = DATA_TYPE_INVERTED[this._originalTypeId];
		this._type = this._originalTypeName;
	}

	get title() {
		const title = [game.i18n.localize('DataInspector.Title')];
		title.push(this.document.name);
		title.push(this.options.path);
		return title.join(': ');
	}

	_initializeApplicationOptions(options) {
		options = super._initializeApplicationOptions(options);

		const documentUuid = options.document.uuid.replace('.', '-');
		const safePath = options.path.replaceAll('.', '-');
		options.uniqueId = 'data-inspector-' + documentUuid + '-' + safePath;

		return options;
	}

	static DEFAULT_OPTIONS = {
		tag: 'form',
		classes: ['koboldworks', 'data-inspector', 'editor', 'standard-form'],
		sheetConfig: false,
		window: {
			resizable: true,
		},
		position: {
			width: 520,
			height: 360,
		},
		form: {
			handler: this._onSubmit,
		},
		actions: {
			delete: this._onDelete,
		},
	};

	static PARTS = {
		form: {
			template: `modules/${CFG.id}/template/editor.hbs`,
		},
		footer: {
			template: 'templates/generic/form-footer.hbs',
		},
	};

	get isUnsupported() {
		const value = fu.getProperty(this.document.toObject(), this.options.path),
			typeId = this._typeId = getTypeId(value);

		return !this.constructor.supportedTypes.includes(typeId);
	}

	async _prepareMainContext(context, options) {
		const doc = this.document,
			value = fu.getProperty(doc.toObject(), this.options.path),
			typeId = this._typeId = getTypeId(value),
			typeName = this._type ?? getTypeName(value),
			originalTypeName = this._originalTypeName;

		context.document = doc;
		context.path = this.options.path;
		context.value = value;
		context.type = typeName;
		context.typeName = typeName;
		context.originalType = originalTypeName;
		context.similarType = originalTypeName === 'string' && ['text', 'html'].includes(typeName);
		context.isHTML = typeName === 'html';
		context.isString = ['html', 'text', 'string'].includes(typeName);
		context.isLongString = typeName === 'html' || typeName === 'text' || originalTypeName === 'string' && (context.value.length > 16 || context.value.indexOf('\n') >= 0);
		context.isUnsupported = this.isUnsupported;
		context.noUpdate = typeName === 'undefined';

		context.isActor = doc instanceof Actor;
		context.isItem = doc instanceof Item;
		context.isEditable = !context.isUnsupported;
		context.isOwner = doc.isOwner;
		context.simpleType = ['number', 'string', 'boolean'].includes(typeName);
		context.deadType = ['null', 'undefined'].includes(typeName);
		context.selectedType = this._type;

		// TODO: i18n
		context.types = {
			number: 'Number',
			boolean: 'Boolean',
			string: 'String',
			null: 'Null',
			undefined: 'Undefined',
			text: 'Text Block',
			html: 'HTML',
		};

		context.field = null;
		switch (typeName) {
			case 'number':
				context.field = new foundry.data.fields.NumberField();
				break;
			case 'string':
				if (context.isHTML) context.field = new foundry.data.fields.HTMLField();
				else context.field = new foundry.data.fields.StringField();
				break;
			case 'boolean':
				context.field = new foundry.data.fields.BooleanField();
				break;
			case 'undefined':
			case 'null':
			case 'object':
				break;
		}

		context.isToken = this._isToken;

		context.inArray = this._inArray;

		context.TYPES = DATA_TYPES;
		return context;
	}

	async _preparePartContext(partId, context, options) {
		switch (partId) {
			case 'form':
				return this._prepareMainContext(context, options);
			case 'footer': {
				const context = {
					buttons: [
						{ type: 'submit', label: 'DataInspector.Update', icon: 'fa-solid fa-floppy-disk', action: 'submit' },
						{ type: 'button', label: 'DataInspector.Delete', icon: 'fa-solid fa-trash', action: 'delete' },
					],
				};

				if (this.isUnsupported) context.buttons.shift();

				return context;
			}
		}
	}

	/**
	 * @this {DataEditor}
	 * @param {Event} event
	 * @param {HTMLElement} elem
	 */
	static _onDelete(event, elem) {
		const doc = this.document,
			path = this.options.path;

		const docData = doc.toObject();

		const parent = getParentProperty(path, docData);
		const update = reconstructParentArrays(docData, parent.path);
		const { updateData, inArray } = update;
		setUpdateData(update, parent, { isDelete: true, inArray });

		console.log('🔎 ⌦ UpdateData:', updateData);

		const app = this;
		Dialog.confirm({
			title: 'Delete data?',
			content: `<p>Delete: <code>${path}</code></p>`,
			yes: () => {
				app.close();
				doc.update(updateData);
			},
			defaultYes: false,
		});
	}

	/**
	 * @param context
	 * @param options
	 */
	_onRender(context, options) {
		super._onRender(context, options);

		const html = this.element.querySelector('.window-content');
		const app = this;

		html.querySelector('select[name="type"]')?.addEventListener('change', (ev) => {
			ev.preventDefault();
			ev.stopPropagation();
			const oldType = this._type,
				newType = ev.target.value;
			this._type = newType;
			// console.log('Changing type from', oldType, 'to', newType);
			this.render();
		});

		// Type swapping
		html.querySelector('select[name="type"]')
			?.addEventListener('change', function (ev) {
				ev.preventDefault();
				ev.stopPropagation();
				app._type = this.value;
				app.editors = {}; // Reset {{editor}}
				app.render(true);
			});
	}

	/**
	 * @this {DataEditor}
	 * @param {Event} event
	 * @param {*} form
	 * @param {*} formData
	 * @returns
	 */
	static _onSubmit(event, form, formData) {
		formData = foundry.utils.expandObject(formData.object);

		// delete formData.type;
		const type = this._type,
			path = this.options.path,
			doc = this.document;

		let value = formData.value;
		if (type === 'undefined') value = undefined;
		else if (type === 'null') value = null;

		console.log('Setting data:', { type, path, value });

		const docData = doc.toObject();

		const parent = getParentProperty(path, docData);
		const update = reconstructParentArrays(docData, parent.path);
		const { updateData } = update;

		update.value = value;
		setUpdateData(update, parent, { isUpdate: true });

		console.log('🔎 ＋ UpdateData:', updateData);

		try {
			doc.update(updateData);
		}
		finally {
			this.close();
		}
	}
}
